.card-bg {
	display: none;
}

.card-bg {
	background-position: center;
	bottom: 0;
	left: 0;
	overflow: hidden;
	position: absolute;
	right: 0;
	top: 0;
}

.card-bg .bg-box {
	height: 100%;
	left: 50%;
	margin: 0 auto;
	position: absolute;
	right: 50%;
}

.bg-img  {
	height: 100%;
	left: -1000px;
	margin: 0 auto;
	position: absolute;
	right: -1000px;
}

.bg-img > img {
	height: 101%;
	width: auto;
}

.card-bg .bg-opacity {
	filter: alpha(opacity=20);
	opacity: .2;
	background: black;
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
}


.vendor-info .vendor-avatar {
	width: 120px;
	left: 0;
	position: absolute;
	right: 0;
	top: -135px;
	margin: 0 auto 15px;
}

.vendor-avatar .avator-outer {
	display: table;
	margin: 0;
	position: relative;
}

.vendor-avatar .avator-outer .avator-align {
	text-decoration: center;
}

.vendor-avatar .avator-outer .avator-align .avator-inner {
	-webkit-border-radius: 50%;
	border-radius: 50%;
	display: table;
	margin-top: 0 auto;
	border: 4px solid #fff;
}

.vendor-avatar .avator-outer .avator-align .avator-inner  > img {
	height: 120px;
	width: 120px;
	border-radius: 50%;
	display: block;
}

.vendor-avatar  .vendor-flag {
	display: block;
	height: 0;
	margin: 0;
}

.vendor-avatar  .vendor-flag  .flag-box {
	left: 102px;
	top: 109px;
	position: absolute;
	height: 15px;
	margin-left: 6px;
	width: 15px;
	display: inline-block;
}

.vendor-avatar  .vendor-flag  .flag-box .flag-icon {
	filter: alpha(opacity=50);
	opacity: .5;
	background: no-repeat url(/img/vflag.png) 0 0;
	height: 18px;
	margin-top: -1px;
	width: 18px;
	position: absolute;
	z-index: 8;
	display: inline-block;
}

.vendor-avatar  .vendor-flag  .flag-box .flag-text {
	display: -moz-inline-box;
	display: inline-block;
	background-color: #e6e6e6;
	color: #36c;
	font-size: 12px;
	overflow: hidden;
	visibility: hidden;
	width: 0;
	height: 15px;
	line-height: 15px;
	margin-left: -7px;
	position: absolute;
	white-space: nowrap;
	z-index: 7;
}

.vendor-avatar  .vendor-flag  .flag-box .flag-box:hover .flag-text {
	visibility: true;
}

.vendor-info .info {
	display: block;
	font-size: 12px;
	height: auto;
	margin: 0;
	text-align: center;
	color: #737373;
}

.vendor-info .info .title {
	display: block;
	white-space: nowrap;
	margin-bottom: 15px;
} 

.vendor-info .info .title h1 {
	cursor: pointer;
	display: inline;
	font-size: 28px;
	font-weight: normal;
	margin: 0;
	max-width: 370px;
	overflow: hidden;
	text-align: left;
	text-overflow: ellipsis;
	white-space: normal;
}

.vendor-info .info .description, .vendor-info .info .roles{
	overflow: hidden;
	text-overflow: ellipsis;
	margin-bottom: 15px;
}

.vendor-info .info .description p {	
	display: block;
	margin: 0 auto;
	overflow: hidden;
	text-overflow: ellipsis;
	max-width: 370px;
}

.vendor-info .social {
	display: block;
	font-size: 12px;
	height: auto;
	margin: 0;
	text-align: center;
	color: #737373;
}

.vendor-actions {
	bottom: 30px;
	display: block;
	left: 0;
	position: absolute;
	right: 0;
	top: auto;

	.actions {
		display: inline-block;
		margin: 0;

		a {
			color: #404040;
			vertical-align: top;
			margin-left: 0;
			margin-right: 15px;
			font-size: 14px;
			cursor: pointer;
		}
	}
}

.vendor-navs {
	height: 44px;
	color: #737373;
	margin:0;
	position: relative;
	text-align: center;
	z-index: 9;
}

.vendor-navs .navbar {
	background: transparent;
	font-family: Roboto,arial,sans-serif;
	font-size: 13px;
	padding: 0;
	background: #f5f5f5;
	margin: 0 auto;
	width: 100%;
}

.vendor-navs .navbar li a {
	display: inline-block;
	padding: 0 12px;
	font-size: 14px;
	line-height: 44px;
	color: #5F5F5F;
	position: relative;
	text-decoration: none;
}

.vendor-navs .navbar li a:hover {
	color: #e9573e;
	background-color: transparent;
}

.vendor-navs .navbar li.active a {
	color: #e9573e;
	background-color: transparent;
}

.vendor-navs .navbar li.active > a:after {
	content: "";
	display: block;
	position: absolute;
	left: -1px;
	right: -1px;
	bottom: -1px;
	border-top: 1px solid #EA714C;
}

.vendor-content {
	margin-top: 30px;
}

.vendor-section {
	margin-top: 15px;
}

